<template>
	<view class="container">
		<view class="order-list">
			<view v-for="order in indents" :key="order.id" class="order-item">
				<view>
					<u--text :text="'订单号:' + order.id" size="40rpx" align="center" color="red"></u--text>
					<view class="flight-info">
						<text class="flight-number">{{ order.flight.flightNumber }}</text>
						<view class="airport-info">
							<text class="departure">{{ order.flight.departureAirport }}</text>
							<u-icon name="arrow-right" color="#2979ff"></u-icon>
							<text class="arrival">{{ order.flight.arrivalAirport }}</text>
						</view>
						<view class="time-info">
							<text>起飞：{{ formatTime(order.flight.departureTime) }}</text>
							<text>到达：{{ formatTime(order.flight.arrivalTime) }}</text>
						</view>
					</view>

					<u--text text="乘客信息" size="30rpx" align="center"></u--text>
					<view class="ticket-list">
						<view v-for="ticket in order.tickets" :key="ticket.id" class="ticket-item">
							<view class="ticket-content">
								<text class="name">{{ ticket.id }}</text>
								<text class="name">{{ ticket.name }}</text>
								<u-tag :text="ticket.seatClass" type="primary" size="mini"></u-tag>
								<text class="price">￥{{ ticket.price.toFixed(2) }}</text>
								<text class="name">{{ ticket.status }}</text>
								<text class="name" v-if="ticket.subFlightId">{{ ticket.subFlight.flightNumber }}</text>
								<text class="name" v-if="ticket.subTicketId">{{ ticket.subTicket.id }}</text>
							</view>
							<view class="action-buttons" v-if="ticket.status == '已支付'">
								<u-button type="error" size="mini"
									@click="refundTicket(order.id, ticket.id)">退票</u-button>
								<u-button type="warning" size="mini"
									@click="showChangeDialog(order.id, ticket.id)">改签</u-button>
							</view>
						</view>
					</view>
					<!-- 订单状态 -->
					<view class="order-footer">
						<text class="total-price">总价：￥{{ (order.totalPrice || 0).toFixed(2) }}</text>
						<u-tag :text="order.status" :type="statusType(order.status)" size="mini"></u-tag>
					</view>
				</view>
			</view>
		</view>

		<!-- 改签弹窗 -->
		<u-modal :show="showChange" :showCancelButton="true" @confirm="submitChange" @cancel="showChange = false"
			title="改签申请">
			<view class="change-form">
				<u--textarea v-model="changeReason" placeholder="请输入改签理由" height="100"></u--textarea>
				<view class="section-title">
					<text class="title-text">选择新航班</text>
				</view>
				<scroll-view :scroll-top="scrollTop" scroll-y="true" class="scroll-Y">
					<radio-group @change="radioChange">
						<label v-for="flight in flights" :key="flight.id" class="flight-option">
							<radio :value="flight.id" :checked="selectedFlight === flight.id" />
							{{ flight.flightNumber }} ({{ flight.departureAirport }} → {{ flight.arrivalAirport }})
						</label>
					</radio-group>
				</scroll-view>
			</view>
		</u-modal>
	</view>
</template>
<script>
	export default {
		data() {
			return {
				indents: [],
				showChange: false,
				changeReason: '',
				selectedFlight: null,
				currentOrderId: null,
				currentTicketId: null,
				flights: [] // 需要从接口获取的航班列表数据
			};
		},
		onShow() {
			this.getData();
			uni.$u.http.get("/flight").then(res => this.flights = res.data);
		},
		onPullDownRefresh() {
			this.getData()
			uni.stopPullDownRefresh()
		},
		methods: {

			getData() {
				uni.$u.http.get("/indent").then(res => {
					this.indents = res.data;
				});
			},

			statusType(status) {
				return status === '已支付' ? 'success' : 'warning';
			},

			refundTicket(orderId, ticketId) {
				uni.showModal({
					title: '确认退票',
					content: '确定要退票吗？',
					success: (res) => {
						if (res.confirm) {
							uni.$u.http.post("/ticket/refund", {
								id: ticketId,
								status: "退票中",
								orderId: orderId
							}).then(res => {
								uni.showToast({
									title: '退票成功',
									icon: 'none',
									duration: 2000
								});
								this.getData()
							});
						}
					}
				});
			},

			showChangeDialog(orderId, ticketId) {
				this.currentOrderId = orderId;
				this.currentTicketId = ticketId;
				this.showChange = true;
			},

			submitChange() {
				if (!this.changeReason || !this.selectedFlight) {
					return uni.showToast({
						title: '请填写完整信息',
						icon: 'none'
					});
				}

				uni.$u.http.post("/ticket/change", {
					id: this.currentTicketId,
					status: "改签中",
					subFlightId: this.selectedFlight,
					remark: this.changeReason,
					orderId: this.currentOrderId
				}).then(res => {
					uni.showToast({
						title: '改签申请已提交',
						icon: 'none',
						duration: 2000
					});
					this.showChange = false;
					this.getData();
				});

			},
			radioChange(e) {
				this.selectedFlight = e.detail.value
			},

			formatTime(time) {
				return uni.$u.timeFormat(time, 'yyyy-mm-dd hh:MM');
			}
		}
	};
</script>

<style scoped>
	.scroll-Y {
		height: 400rpx;
	}

	.ticket-list {
		background-color: #f9f9f9;
	}

	/* 新增样式调整 */
	.container {
		padding: 20rpx;
		background-color: #f5f5f5;
	}

	.order-item {
		margin-bottom: 40rpx;
		background-color: white;
		box-shadow: 1px 1px 1px 1px white;
		padding: 20rpx;
	}

	.section-title {
		margin: 30rpx 0 20rpx;
		position: relative;
	}

	.title-text {
		font-size: 32rpx;
		font-weight: bold;
		color: #333;
	}

	.title-line {
		height: 2rpx;
		background-color: #2979ff;
		margin-top: 10rpx;
		width: 80rpx;
	}

	.flight-info {
		padding: 20rpx;
		border-radius: 8rpx;
	}

	.flight-number {
		font-size: 34rpx;
		color: #2979ff;
		font-weight: bold;
	}

	.airport-info {
		display: flex;
		align-items: center;
		margin: 20rpx 0;
		font-size: 30rpx;
	}

	.time-info {
		display: flex;
		flex-direction: column;
		color: #666;
		font-size: 26rpx;
	}

	.ticket-item {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20rpx 0;
		border-bottom: 1rpx solid #eee;
	}

	.ticket-content {
		flex: 1;
		display: flex;
		align-items: center;
		gap: 20rpx;
	}

	.action-buttons {
		display: flex;
		gap: 10rpx;
	}

	.order-footer {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 30rpx;
		padding-top: 20rpx;
		background-color: #f9f9f9;
	}

	.total-price {
		color: #e64340;
		font-size: 30rpx;
		font-weight: bold;
	}

	.change-form {
		width: 100%;
		padding: 20rpx;
	}

	.flight-option {
		display: block;
		padding: 20rpx;
		border-bottom: 1rpx solid #eee;
	}
</style>